
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  color: #f4f4f4;
  width: 400px;
}
a {
  color: #f4f4f4;
  text-decoration: none;
}
.toggle-row {
  height: 32px;
  border: 1px solid;
  display: flex;
  background: #0d2647;
}
.toggle-row a {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: stretch;
}
.toggle-row a:last-child {
  border-left: 1px solid;
}
.folder-container {
  position: relative;
  display: flex;
  justify-content: center;
}
.folder-container .deco-border {
  position: absolute;
  left: 0;
  top: 8px;
  width: 100%;
  border-bottom: 1px solid #39a;
}
.folder-container h3 {
  top: 0;
  font-size: 14px;
  position: relative;
  margin: 0;
  font-weight: normal;
  background-color: #0d2647;
  padding: 0 8px;
}
.settings {
  padding: 8px;
  background: #0d2647;
}
.header {
  text-align: right;
  font-size: 18px;
  padding-bottom: 4px;
}
.footer {
  padding-top: 8px;
  border-bottom: 1px solid;
  font-size: 12px;
  margin-bottom: 8px;
  padding-bottom: 8px;
}
.row {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}
.row .label {
  width: 100px;
}
.row .value {
  flex: 1;
}
.row .value input[type='number'] {
  width: 100%;
}
.row select {
  width: 100%;
}
.source {
  position: fixed;
  bottom: 16px;
  right: 16px;
  font-size: 12px;
}
@media screen and (max-width: 500px) {
#app {
    width: 100%;
    max-width: 100%;
}
}